import imgbackground from '../img/home_background_1.png';

import styled from "styled-components";

import Header from "../Component/Header";

const HomeWrapper = styled.div`
  position: relative;
  width: 100%;
  background: linear-gradient( #466d89, #041527);
  // min-height: 100vh;
  overflow: hidden;
`

const ImgBackground = styled.img`
    background-size: 100% 100%;
    background-repeat: no-repeat;
    z-index: 0;
    &.background{
      position: absolute;
      top: 0;
      right: 0;
      width: 1920px;
      height: 1080px;
    }
`;

const Headerclass = styled.div`
    height: 90px;
    max-width: 1920px;
    margin: auto;
`;

const BodyContent = styled.div`
position: relative; /*     */
  z-index: 1;
  text-align: center;
  padding: 20px;
  background : None;
`
const BodyText = styled.div`
   position: relative; /*     z-index  */
    z-index: 1; /*     */
    color: white; /*      */
    font-size: 40px;
    font-weight: bold;
    text-align: center;
    width: 600px;
    background: None;
    padding: 20px;

`

const TextSet = styled.div`

    display: flex;
    justify-content: center;
    align-items: center;
    // width : 800px;
    // height: 150px;
    // max-width: 600px;
    // max-height : 600px;
`

function Home() {
    return (
          <HomeWrapper>
            <Headerclass>
                <Header />
            </Headerclass>
            <ImgBackground className='background' src ={imgbackground}></ImgBackground>
            <BodyContent className='App'>
              <TextSet>
              <BodyText className = "App-header">    <br/>  <br/>   .</BodyText>
              </TextSet>
            </BodyContent>
          </HomeWrapper>
    );
  }
  
  export default Home;